import React, { Component } from 'react';
import './App.css';
import img_elBackground2 from './images/HomeScreen_elBackground2_707592.jpg';
import Label from './Label';
import Login from './Login';


export default class HomeScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: '#f6f6f6',
     };
    const style_elBackground2 = {
      backgroundImage: 'url('+img_elBackground2+')',
      backgroundRepeat: 'no-repeat',
      backgroundPosition: '50% 50%',
      backgroundSize: 'cover',
     };
    
    return (
      <div className="AppScreen HomeScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elBackground' style={style_elBackground_outer}>
            <div className="appBg" style={style_elBackground} />
          </div>
          
          <div className="containerMinHeight elBackground2" style={style_elBackground2} />
        </div>
        
        <div className="layoutFlow" style={layoutFlowStyle}>
          <div className='hasNestedComps elColumns'>
            <div className="">
              <div className="col0">
                <Label appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
              </div>
              <div className="col1">
                <Login appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
              </div>
            </div>
          </div>
        </div>
        
      </div>
    )
  }
  

}
