import React, { Component } from 'react';
import './App.css';
import BarraLateraleSinistra from './BarraLateraleSinistra';


export default class RicercaScreen extends Component {

  // Properties used by this component:
  // appActions, deviceInfo

  render() {
    let layoutFlowStyle = {};
    let baseStyle = {};
    if (this.props.transitionId && this.props.transitionId.length > 0 && this.props.atTopOfScreenStack && this.props.transitionForward) {
      baseStyle.animation = '0.25s ease-in-out '+this.props.transitionId;
    }
    if ( !this.props.atTopOfScreenStack) {
      layoutFlowStyle.height = '100vh';
      layoutFlowStyle.overflow = 'hidden';
    }
    
    const style_elBackground = {
      width: '100%',
      height: '100%',
     };
    const style_elBackground_outer = {
      backgroundColor: 'white',
     };
    const style_elComp = {
      width: '100%',
      height: '100%',
     };
    const style_elComp_outer = {
      backgroundColor: '#fefcff',
     };
    
    return (
      <div className="AppScreen RicercaScreen" style={baseStyle}>
        <div className="background">
          <div className='containerMinHeight elBackground' style={style_elBackground_outer}>
            <div className="" style={style_elBackground} />
          </div>
        </div>
        
        <div className="screenFgContainer">
          <div className="foreground">
            <div className='hasNestedComps containerMinHeight elComp' style={style_elComp_outer}>
              <div className="" style={style_elComp}>
                <BarraLateraleSinistra ref={(el)=> this._elComp = el} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
              </div>
            </div>
            
          </div>
        </div>
      </div>
    )
  }
  

}
