import React, { Component } from 'react';
import './App.css';
import Ricerca2 from './Ricerca2';
import EsciRicerca from './EsciRicerca';


export default class BarraDiRicerca extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      elBarraRicerca_visible: true,
      elComp_visible: true,
    };
  }

  onClick_elComp = (ev) => {
    this.setState({elBarraRicerca_visible: false});
  
    this.setState({elComp_visible: !this.state.elComp_visible});
  
  }
  
  
  render() {
    const style_elBarraRicerca = {
      boxShadow: '0.0px 2.3px 18px rgba(0, 0, 0, 0.1600)',
     };
    const elBarraRicerca = this.state.elBarraRicerca_visible ? (
      <div className="hasNestedComps containerMinHeight elBarraRicerca" style={style_elBarraRicerca}>
        <Ricerca2 ref={(el)=> this._elBarraRicerca = el} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
      </div>
     ) : null;
    const style_elComp = {
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const elComp = this.state.elComp_visible ? (
      <div className="hasNestedComps elComp" style={style_elComp} onClick={this.onClick_elComp} >
        <EsciRicerca ref={(el)=> this._elComp = el} appActions={this.props.appActions} deviceInfo={this.props.deviceInfo} locStrings={this.props.locStrings} />
      </div>
     ) : null;
    
    return (
      <div className="BarraDiRicerca">
        <div className="foreground">
          { elBarraRicerca }
          { elComp }
        </div>
      </div>
    )
  }
  

}
