import React, { Component } from 'react';
import './App.css';
import btn_icon_27838 from './images/btn_icon_27838.png';

// UI framework component imports
import Button from 'muicss/lib/react/button';


export default class Ricerca2 extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      field: '',
    };
  }

  textInputChanged_field = (event) => {
    this.setState({field: event.target.value});
  }
  
  render() {
    const style_elRectangle = {
      background: 'rgba(213, 213, 213, 1.000)',
     };
    const style_elField = {
      display: 'block',
      backgroundColor: 'white',
      paddingLeft: '1rem',
      boxSizing: 'border-box', // ensures padding won't expand element's outer size
      fontSize: 12.3,
      pointerEvents: 'auto',
     };
    
    const style_elIconButton = {
      display: 'block',
      backgroundImage: 'url('+btn_icon_27838+')',
      backgroundRepeat: 'no-repeat',
      backgroundSize: '35.809%',
      paddingTop: '30%',
      backgroundPosition: '50% 0%',
      fontSize: 11.3,
      color: 'black',
      textAlign: 'center',
      backgroundColor: 'transparent',
     };
    const style_elButton = {
      display: 'block',
      textTransform: 'none',
      fontSize: 9.4,
      color: '#fff',
      textAlign: 'center',
      backgroundColor: '#868686',
     };
    const style_elButtonCopy = {
      display: 'block',
      textTransform: 'none',
      fontSize: 9.4,
      color: 'white',
      textAlign: 'center',
     };
    
    return (
      <div className="Ricerca2">
        <div className="foreground">
          <div className="containerMinHeight elRectangle" style={style_elRectangle} />
          <input className="font-avenirLight  elField" style={style_elField} type="text" placeholder={this.props.locStrings.barraricerca_field_963712} onChange={this.textInputChanged_field} value={this.state.field}  />
          <button className="font-avenirLight  elIconButton" style={style_elIconButton}>
            {this.props.locStrings.Filtro}
          </button>
          <Button className="font-avenirLight  elButton" style={style_elButton}>
            {this.props.locStrings.barraricerca_button_854298}
          </Button>
          <Button className="font-avenirLight  elButtonCopy" style={style_elButtonCopy}  color="primary" >
            {this.props.locStrings.barraricerca_button_902103}
          </Button>
        </div>
      </div>
    )
  }
  

}
