import React, { Component } from 'react';
import './App.css';

// UI framework component imports
import Button from 'muicss/lib/react/button';
import Input from 'muicss/lib/react/input';


export default class Login extends Component {

  // This component doesn't use any properties

  constructor(props) {
    super(props);
    
    this.state = {
      field: '',
      fieldCopy: '',
    };
  }

  onClick_elButton = (ev) => {
    // Go to screen 'Ricerca'
    this.props.appActions.goToScreen('ricerca', { transitionId: 'fadeIn' });
  
  }
  
  
  textInputChanged_field = (event) => {
    this.setState({field: event.target.value});
  }
  
  textInputChanged_fieldCopy = (event) => {
    this.setState({fieldCopy: event.target.value});
  }
  
  render() {
    const style_elRectangle = {
      background: 'rgba(252, 255, 255, 1.000)',
      boxShadow: '0.0px 2.0px 3px rgba(0, 0, 0, 0.3000)',
     };
    const style_elButton = {
      display: 'block',
      textTransform: 'none',
      color: '#fff',
      textAlign: 'center',
      backgroundColor: '#3faafe',
      cursor: 'pointer',
      pointerEvents: 'auto',
     };
    const style_elTextBlock = {
      color: 'black',
      textAlign: 'center',
     };
    const style_elField = {
      display: 'block',
      backgroundColor: 'transparent',
      borderColor: 'transparent',
      pointerEvents: 'auto',
     };
    
    const style_elFieldCopy = {
      display: 'block',
      backgroundColor: 'transparent',
      borderColor: 'transparent',
      pointerEvents: 'auto',
     };
    
    
    return (
      <div className="Login">
        <div className="foreground">
          <div className="elRectangle" style={style_elRectangle} />
          <Button className="actionFont elButton" style={style_elButton} onClick={this.onClick_elButton} >
            {this.props.locStrings.comp2_button_469991}
          </Button>
          <div className="baseFont elTextBlock" style={style_elTextBlock}>
            <div>{this.props.locStrings.comp2_textblock_690723}</div>
          </div>
          <Input className="baseFont elField" style={style_elField} type="text" hint={this.props.locStrings.comp2_field_167882} onChange={this.textInputChanged_field} value={this.state.field}  />
          <Input className="baseFont elFieldCopy" style={style_elFieldCopy} type="password" hint={this.props.locStrings.comp2_field_152481} onChange={this.textInputChanged_fieldCopy} value={this.state.fieldCopy}  />
        </div>
      </div>
    )
  }
  

}
